import React from 'react';
import { Link } from 'react-router-dom';
import 'font-awesome/css/font-awesome.min.css';
import jsonResponse from '../../data/projects';

const load = () => JSON.parse(JSON.stringify(jsonResponse))
const Projects = ({ match }) => {
    let { projects } = load().data;
    let newArray = [];
    while (projects.length) {
        newArray.push(projects.splice(0, 3));
    }
    console.log(newArray);
    let renderElement = newArray.map((array, index)=>
        <div key={index} className="row">
            {array.map((project, index) => (
                <div key={project.id} className="col-sm-4">
                    <Link to={`projects/${project.id}`}>
                        <div className="card text-white bg-primary mb-3" >
                            <div className="card-header"> <i className={project.icon}></i></div>
                            <div className="card-body">
                                <h5 className="card-title">{project.name}</h5>
                            </div>
                        </div>
                    </Link>
                </div>))}
        </div>
    )
 
    return (

        <div className="conatiner" style={{ align: 'centre', width: '80%', height: '50%', padding: '10px', margin: 'auto' }}>
            <div className="card text-left bg-secondary text-white" style={{ marginBottom: '10px' }}>
                <div className="card-body">
                    <h3 className="text-white">Projects</h3>
                </div>
            </div>
            {renderElement}

        </div>
    )
}


export default Projects
